.vertical-tabs .nav-pills {
  display: none;
}
@media (min-width: 768px) {
  .vertical-tabs .nav-pills {
    display: flex;
  }
  .vertical-tabs .tab-pane .border {
    display: none;
  }
  .vertical-tabs .tab-pane .collapse {
    display: block;
  }
}
@media (max-width: 767px) {
  .vertical-tabs .tab-content > .tab-pane {
    display: block !important;
    opacity: 1;
  }
}